		img {
			height: 36px;
		}
		@media (max-width: 991.98px) {
			display: block;
		}
	}
}

.masthead-dropdown {
	position: relative;
	display: inline-block;
	.dropdown-content {
		display: none;
		position: absolute;
		right: 0;
		left: auto;
		z-index: 1000;
		// float: left;
		min-width: 160px;
		// padding: 0.5rem 0;
		// margin: 0.125rem 0 0;
		font-size: 1rem;
		text-align: left;
		list-style: none;
		background-color: #fff;
		background-clip: padding-box;
		border: 1px solid $color-main-light;
		border-radius: 0.25rem;
		&:before {
			content: "";
			position: absolute;
			top: -8px;
			right: 32px;
			width: 0; 
			height: 0; 
			border-left: 8px solid transparent;
			border-right: 8px solid transparent;
			border-bottom: 8px solid darken($color-main-light, 5%);
		}
		&:after {
			content: "";
			position: absolute;
			top: -7px;
			right: 33px;
			width: 0; 
			height: 0; 
			border-left: 7px solid transparent;
			border-right: 7px solid transparent;
			border-bottom: 7px solid #fff;
		}
	}
	.dropdown-row {
		&:after {
			display: block;
			clear: both;
			content: "";
		}
		.dropdown-col {
			margin: 0;
			padding: 0;
			list-style: none;
			float: left;
			width: 50%;
			.item {
				color: $color-chocolate;
				padding: 0.5rem 1rem;
				display: block;
				width: 100%;
				// white-space: nowrap;
				&:hover {
					background-color: $color-main-light;
				}
			}
		}
		&.about {
			width: 320px;
			.dropdown-col:first-child {
				width: 38%;
			}
			.dropdown-col:last-child {
				width: 62%;
			}
		}
		&.lang {
			width: 200px;
		}
		@media (max-width: 767.98px) {
			&.about, &.lang {
				width: 160px;
				.dropdown-col:first-child,
				.dropdown-col:last-child {
					width: 100%;
				}
			}
		}
	}
	&:hover {
		.dropdown-content { display: block;}
	}
}
.masthead-block-menu, a.masthead-block-menu {
