.masthead-search-wrapper {
	order: 1;
	position: relative;
	.form-control {
		height: calc(1.625em + .75rem + 2px);
		border-radius: 25px;
		padding-left: 1.25rem;
		padding-right: 3rem;
		width: 25rem;
		@media (max-width: 1199.98px) {
