		}
	}
	.btn {
		color: $color-chocolate;
		font-size: 1.125rem;
		padding-right: 1rem;
		border-radius: 25px;
		position: absolute;
		top: 0;
		right: .25rem;
	}
	@media (max-width: 767.98px) {
		order: initial;
		// background-color: $color-main;
		position: absolute;
		z-index: 999;
		top: 68px;
		&.px-fluid {
			right: 1rem;
			left: 1rem;
		}
		&.px-shrink {
			right: 12%;
			left: 12%;
			margin-left: 36px;
		}
		.form-control {
			width: 100%;
		}
	}
