		position: absolute;
		top: calc(50% - 27px);
		right: 0;
		left: 0;
		height: 54px;
		margin: 0;
		z-index: -1;
		// border: 1px solid red;
	}
	.owl-prev,
	.owl-next { 
		@include myicon;
		position: absolute;
		margin: 0 !important;
	}
	.owl-prev { left: -36px; }
	.owl-next { right: -36px; }
	.owl-prev:before {
		content: "\e935";
	}
	.owl-next:before {
		content: "\e936";
	}
	.owl-prev:hover,
	.owl-next:hover {
		background: transparent !important;
		color: rgba($color-main, .5) !important;
	}
	.owl-prev span,
	.owl-next span {
		display: none;
	}
	@media (max-width: 575.98px) {
		.owl-nav {
			font-size: 24px;
			top: calc(50% - 18px);
