/* customed input radio&checkbox begin */
.custom-input-ctn {
  display: block;
  position: relative;
  padding-left: 22px;
  cursor: pointer;
  font-size: inherit;
  font-weight: normal;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.form-inline .custom-input-ctn,
.form-inline .custom-select {
	display: inline-block;
}
.custom-input-ctn input {
  /* Hide the browser's default property */
  position: absolute;
  opacity: 0;
  cursor: pointer;
}
.radiomark,
.checkmark {
	/* Create a custom radio&checkbox button */
	position: absolute;
	top: 12px;
	left: 0;
	height: 16px;
	width: 16px;
	border-radius: 50%;
	margin-top: -8px;
	background-color: #fff;
	border: 1px solid $color-main-tint-40;
}
.checkmark {
	border-radius: 0;
}
.custom-input-ctn:hover input ~ .radiomark,
.custom-input-ctn:hover input ~ .checkmark {
    background-color: #fff;
    border: 1px solid darken($color-main-tint-40, 10%);
}
.radiomark:after,
.checkmark:after {
	/* Create the custom radio&checkbox indicator (hidden when not checked) */
	content: "";
	position: absolute;
	display: none;
}
.custom-input-ctn input:checked ~ .radiomark:after,
.custom-input-ctn input:checked ~ .checkmark:after {
	/* Show the custom radio&checkbox indicator when checked */
	display: block;
}
.custom-input-ctn .radiomark:after {
	/* Style the custom radio indicator */
	top: 50%;
	left: 50%;
	width: 8px;
	height: 8px;
	margin: -4px 0 0 -4px;
	border-radius: 50%;
	background: $color-main;
}
.custom-input-ctn .checkmark:after {
	/* Style the custom checkbox indicator */
	top: 50%;
	left: 50%;
	width: 7px;
	height: 12px;
	margin: -7px 0 0 -3px;
	border: solid $color-main;
	border-width: 0 3px 3px 0;
	-webkit-transform: rotate(45deg);
	-ms-transform: rotate(45deg);
	transform: rotate(45deg);
}
/* customed input radio&checkbox end */

/* customed input select begin */
.customized-select {
	position: relative;
}
.customized-select select {
	display: none;
}
.select-selected {
	background-color: #fff;
	border-radius: 4px;
	border: 1px solid #ced4da;
}
.select-selected:after {
	/* style the arrow inside the select element: */
	position: absolute;
	@include myicon;
	content: "\e938";
	font-size: 18px;
    top: 10px;
    right: 12px;
}
.select-selected.select-arrow-active:after {
	/* point the arrow upwards when the select box is open (active): */
	@include myicon;
	content: "\e937";
}
.select-items div,
.select-selected {
	/* style the items (options), including the selected item: */
	// color: $color-gray-666;
	padding: 6px 42px 6px 12px;
	cursor: pointer;
	user-select: none;
}
.select-items {
	/* style items (options): */
	position: absolute;
	background-color: #eee;
	border-radius: 0 0 4px 4px;
	top: 100%;
	left: 0;
	right: 0;
	z-index: 99;
}
.select-hide {
	/* hide the items when the select box is closed :*/
	display: none;
}
.select-items div:hover, .same-as-selected {
	background-color: rgba(#fff, .3);
}
/* customed input select end */
