	color: #fff;
	padding: 2px 6px;
	background-color: $color-milo;
	border-radius: 2px;
	display: inline-block;
}
.search-content-grid-wrapper {
	// padding: 20px 14px;
	padding-top: 20px;
	padding-bottom: 20px;
	// [class*="col-"] {
	// 	padding-right: 6px;
	// 	padding-left: 6px;
	// }
	.list-group img {
		border-radius: 4px 4px 0 0;
	}
	.list-group-item {
		color: $color-gray-666;
		line-height: 1.5;
	}
	.tools-intro-wrapper {
		margin-top: 0;
		margin-bottom: 20px;
	}
	.taxon-tree-container a {
		&[aria-expanded="true"]:after,
		&[aria-expanded="false"]:after,
		&[aria-expanded="final"]:after {
			top: 7px;
		}
	}
	.pagination {
		margin: 24px 0;
	}
	.scientific-name-wrapper {
		// font-size: 14px;
		// line-height: 1.5;
		// a {
		// 	color: $color-gray-666;
		// }
	}
}
.listbox-img-right-wrapper {
	margin: 15px 0;
	padding-bottom: 15px;
	border-bottom: 1px solid $color-gray-ddd;
	@media (max-width: 767px) {
		margin-left: 0;
		margin-right: 0;
	}
	.search-content-list-tag {
		margin-bottom: 2px;
	}
}
.listbox-inner-date {
	font-size: 12px;
	color: $color-gray-888;
	display: inline-block;
	margin: 4px 0;
}
.listbox-inner-title {
	font-size: 16px;
	color: $color-chocolate;
	font-weight: normal;
	margin: 6px 0;
	a {
		color: darken($color-chocolate, 10%);
		// color: $color-brick;
		display: block;
		&:hover {
			color: $color-main;
			@include transition;
		}
		@media (max-width: 767px) {
			height: 1.1em;
			white-space: nowrap;
			text-overflow: ellipsis;
			overflow: hidden;
		}
	}
}
.listbox-inner-summary {
	font-size: 14px;
	margin: 4px 0;
	color: $color-gray-666;
	a {
		color: $color-gray-666;
		// @media (max-width: 767px) {
		// 	display: block;
		// 	height: 3em;
		// 	overflow: hidden;
		// }
		&:hover {
			color: $color-gray-888;
			@include transition;
		}
	}
}
.listbox-inner-tag {
	font-size: 12px;
	color: $color-gray-888;
	background-color: $color-main-tint-10;
	margin-right: 2px;
	border-radius: 10px;
	display: inline-block;
	a {
		color: $color-gray-888;
		line-height: 20px;
		height: 20px;
		padding: 0 10px;
	}
}
.pagination {
	margin: 30px auto;
	border-radius: 0;
	li {
		&:first-child a {
			border-top-left-radius: 30px;
			border-bottom-left-radius: 30px;
		}
		&:last-child a {
			border-top-right-radius: 30px;
			border-bottom-right-radius: 30px;
		}
		&.active a {
			color: #fff;
			background-color: $color-milo;
			border-color: $color-milo;
			border-radius: 30px;
		}
		&.disabled a {
			background-color: transparent;
			border-color: transparent;
		}
	}
	a {
		padding: 4px 12px;
		color: $color-chocolate;
		background-color: transparent;
		border: none;
		margin: 0 3px;
		border-radius: 30px;
		&:hover, &:focus {
			color: #fff;
			background-color: rgba($color-milo, .5);
			border-color: rgba($color-milo, .5);
			border-radius: 30px;
		}
	}
}
.col-scatter-filter {
	border: 1px solid #ddd;
	border-radius: .375rem;
	-ms-flex: 0 0 350px;
	flex: 0 0 350px;
	max-width: 350px;
	padding-top: 1.25rem;
	padding-bottom: 1.25rem;
	.text {
		color: $color-chocolate;
		margin: .25rem;
	}
	.amount {
		color: $color-brick;
		font-size: 1.25rem;
		margin: .25rem;
	}
	.scatter-icon-list {
		margin: .5rem 0 1rem 0;
		padding: .25rem 0;
		list-style: none;
		display: -ms-flexbox;
		display: flex;
		-ms-flex-wrap: wrap;
		flex-wrap: wrap;
		margin-right: -5px;
		margin-left: -5px;
		border-bottom: 1px solid #ddd;
		li {
			margin-bottom: 8px;
			padding: 0;
			-ms-flex: 0 0 20%;
			flex: 0 0 20%;
			max-width: 20%;
			text-align: center;
		}
	}
	.myicon {
		font-size: 1.625rem;
		color: $color-chocolate;
		padding: 1rem;
		background-color: $color-main-light;
		border-radius: 100%;
		display: inline-block;
		&.disable {
			color: rgba($color-chocolate, .4);
			background-color: $color-main-tint-04;
		}
	}
	.link-chocolate {
		display: block;
		color: $color-chocolate;
		text-align: center;
		padding: .75rem .5rem .5rem;
		border-top: 1px solid #ddd;
		// margin-top: .5rem;
		.myicon {
			font-size: inherit;
			color: inherit;
			padding: 0;
			background-color: transparent;
		}
		&:hover {
			color: $color-main;
		}
	}
	@media (max-width: 767.98px) {
		-ms-flex: 0 0 calc(100% - 30px);
		flex: 0 0 calc(100% - 30px);
		max-width: calc(100% - 30px);
		margin: 15px;
	}
}
.col-scatter-map {
	-ms-flex: 0 0 calc(100% - 350px);
	flex: 0 0 calc(100% - 350px);
	max-width: calc(100% - 350px);
	@media (max-width: 767.98px) {
		-ms-flex: 0 0 100%;
		flex: 0 0 100%;
		max-width: 100%;
	}
}

#occurence-charts-dataset-table .input-group-text {
